.cardboard {
    width: 100%;
    flex-grow: 1;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    align-items: center;
}

.message {
    font-size: 1.5em;
    text-align: center;
    color: #FFFFFF;
}

.deck {
    width: 90%;
    height: 90vw;
    background-color: #a8b5ca;
    border-radius: 4%;
}

.title {
    font-size: 2em;
    text-align: center;
    color: #3aaef0;
}
